.section {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.button {
  margin-top: 20px;
  margin-bottom: 40px;
  width: 336px;
  height: 45px;
  border: 2px solid #009990;
  border-radius: 5px;
  font-family: 'Roboto';
  color: #FFFFFF;
  font-size: 16px;
  line-height: 19px;
  background-color: #009990;
  cursor: pointer;
  transition: .5s;
}

.button:hover {
  background-color: #ace4e1;
  border: 2px solid #ace4e1;
  color: #009990;
}